<template>
  <div class="pg-unitive-signup theme--www">
    <header class="header--mini">
      <div class="wrapper cf">
        <a class="site-logo" href="http://www.meituan.com">美团</a>
        <div class="login-block">
          <span class="tip">已有美团账号？</span>
          <router-link class="btn btn-small login" :to="{name:'login'}">登录</router-link>
        </div>
      </div>
    </header>

    <div class="content">
      <div class="J-unitive-signup-form">
        <div class="sheet" style="display:block">
          <div>
            <div class="form-field form-field--mobile">
              <label>手机号</label>

              <input
                type="text"
                name="mobile"
                class="f-text J-mobile"
                v-model="phone"
                @focus="phoneText=false"
                @blur="phoneHandler"
              />

              <span class="J-unitive-tip unitive-tip" v-if="phoneText">注册成功后，全美团通用</span>
              <span class="inline-tip" v-if="errText.phone.length>0">
                <i class="tip-status tip-status--opinfo"></i>
                {{errText.phone}}
              </span>
              <span class="inline-tip" v-if="success.phone">
                <i class="tip-status tip-status--success"></i>
              </span>
            </div>
            <div class="form-field form-field--vbtn">
              <label>图片验证码</label>

              <div class="J-captcha captcha-wrapper" style="display:none" hidden="true">
                <input
                  type="text"
                  id="captcha-mobile"
                  class="f-text J-captcha"
                  name="captcha-mobile"
                  autocomplete="off"
                />
                <img
                  id="signup-captcha-mobile-img"
                  height="34px"
                  width="60px"
                  class="signup-captcha-img J-captcha-refresh"
                  src="https://passport.meituan.com/account/captcha"
                />
                <a
                  tabindex="-1"
                  class="J-captcha-refresh captcha-refresh inline-link"
                  href="javascript:void(0)"
                >看不清楚？换一张</a>
                <span class="J-captcha-tip inline-tip"></span>
              </div>
              <div class="verify-wrapper">
                <input
                  type="button"
                  class="btn-normal btn-mini verify-btn J-verify-btn"
                  value="免费获取短信动态码"
                />
                <span class="f1 verify-tip" id="J-verify-tip"></span>
              </div>
            </div>
            <div class="form-field form-field--sms">
              <label>短信动态码</label>
              <input
                type="text"
                name="verifycode"
                class="f-text J-sms"
                v-model="sms"
                @blur="smsHandler"
              />
              <span class="inline-tip" v-if="errText.sms>0">
                <i class="tip-status tip-status--opinfo"></i>
                {{errText.sms}}
              </span>
              <span class="inline-tip" v-if="success.sms">
                <i class="tip-status tip-status--success"></i>
              </span>
            </div>
            <div class="form-field form-field--pwd">
              <div class="pw-strength">
                <div
                  class="pw-strength__bar"
                  :class="{
                  'pw-strength__bar--weak':psStrong==85,
                  'pw-strength__bar--normal':psStrong==172,
                  'pw-strength__bar--strong':psStrong==260
                }"
                  id="J-pw-strength__bar"
                  :style="{width:psStrong+'px'}"
                ></div>
                <div class="pw-strength__letter">
                  <span class="pw-strength__label">弱</span>
                  <span class="pw-strength__label">中</span>
                  <span class="pw-strength__label pw-strength__label--noborder">强</span>
                </div>
              </div>
              <label>创建密码</label>
              <input
                type="password"
                name="password"
                class="f-text J-pwd"
                v-model="password"
                @input="passwordHandler"
              />
              <span class="inline-tip" v-if="errText.password.length>0">
                <i class="tip-status tip-status--opinfo"></i>
                {{errText.password}}
              </span>
              <span class="inline-tip" v-if="success.password">
                <i class="tip-status tip-status--success"></i>
              </span>
            </div>
            <div class="form-field form-field--pwd2">
              <label>确认密码</label>
              <input
                type="password"
                name="password2"
                class="f-text J-pwd2"
                v-model="password2"
                @blur="password2Handler"
              />
              <span class="inline-tip" v-if="errText.password2.length>0">
                <i class="tip-status tip-status--opinfo"></i>
                {{errText.password2}}
              </span>
              <span class="inline-tip" v-if="success.password2">
                <i class="tip-status tip-status--success"></i>
              </span>
            </div>
            <div class="slider-verify-wrapper" style="display:none">
              <div class="slider-verify-pop-bg"></div>
              <div id="slider-verify"></div>
            </div>
            <div class="form-field">
              <input
                data-mtevent="signup.submit"
                type="submit"
                name="commit"
                class="btn"
                value="同意以下协议并注册"
                @click="submit"
              />
            </div>
            <input type="hidden" name="fingerprint" class="J-fingerprint" value="0-1-1-" />
