<script>
import eduApi from "@/ajax/eduApi.js";
import textPassword from "@/components/test.js";
export default {
  data() {
    return {
      phoneText: true,
      phone: "",
      password: "",
      password2: "",
      sms: "",
      psStrong: 0,
      errText: {
        phone: "",
        password: "",
        password2: "",
        sms: "",
      },
      success: {
        phone: false,
        password: false,
        password2: false,
        sms: false,
      },
    };
  },
  methods: {
    phoneHandler() {
      this.success.phone = false;
      if (!this.phone.length == 11) {
        this.errText.phone = "请输入11位的手机号";
        return;
      }
      const regPhone = /^1[\d]{10}$/g;
      if (!regPhone.test(+this.phone)) {
        this.errText.phone = "请输入正确的手机号";
        return;
      }
      this.success.phone = true;
      this.errText.phone = "";
    },
    smsHandler() {
      this.success.sms = false;
      if (!this.phone.length == 6) {
        this.errText.sms = "请输入6位数字验证码";
        return;
      }
      const reg = /^[\d]{6}$/g;
      if (!reg.test(+this.sms)) {
        this.errText.sms = "请输入6位数字验证码";
        return;
      }
      this.success.sms = true;
      this.errText.sms = "";
    },
    passwordHandler() {
      if (!this.password) {
        this.psStrong = 0;
      }
      this.success.password = false;
      this.errText.password = "";
      if (this.password.length < 8) {
        this.errText.password = "密码少于8位";
        return;
      } else if (this.password.length > 16) {
        this.errText.password = "密码大于16位";
        return;
      } else if (/[^._~!@#$^&*a-z0-9A-Z]/.test(this.password)) {
        this.errText.password = "密码包含非法字符";
        return;
      }
      this.success.password = true;
      this.errText.password = "";
      const strong = textPassword(this.password);
      // console.log(strong);
      switch (strong) {
        case 1:
          this.psStrong = 85;
          break;
        case 2:
          this.psStrong = 172;
          break;
        case 3:
          this.psStrong = 260;
          break;
      }
    },
    password2Handler() {
      this.success.password2 = false;
      if (this.password !== this.password2) {
        this.errText.password2 = "密码不一致";
        return
      }
      this.success.password2 = true;
      this.errText.password2 = "";
    },
    submit() {
      eduApi
        .register({
          userName: this.phone,
          password: this.password,
          rePassword: this.password2,
        })
        .then((req) => {
          this.$store.commit('setUseName',req.data.data);
          this.$router.push({name:'home'})

        }).catch(err=>{
          alert(req.data.msg)
        })
    },
  },
};
</script>
